@import '../../styles/index.scss';

.ViewContainer{
    // min-height: 1500px;
}

.ViewHeaderTitle{
    text-align: center;
    color: $accent-color;
    font-size: 4vw;
    font-weight: 300;
    width: 100%;
}

.ViewHeaderBlurb{
    color: $secondary-color;
    font-size: 1.6vw;
    font-weight: 600;
    display: block;
    // text-align: center;
    @include MarginTopBottom(3vh);
    margin-left: auto;
    margin-right: auto;
}

.ViewHeaderImageContainer{
    @include MarginTopBottom(4vh);
    text-align: center;
    position: relative;
    border: 3px solid $secondary-color;
    border-radius: 5px;
    height: 60vh;
    filter: grayscale(0.6);
}

.ViewHeaderImage{
    width: 100%;
    height: 100%;
    opacity: 0.8;
}
